/*
 > sepcial classes
*/
// drop down arrow class
.down{
    position: relative;
    cursor: pointer;
    &:after{
        content: '\f107';
        font-family: 'Font Awesome 5 free';
        font-style: normal;
        font-weight: 600;
        position: absolute;
        right: 2px;
        top: 27px;
    }
}
.down-space{
    position: relative;
    cursor: pointer;
    &:after{
        content: '\f107';
        font-family: 'Font Awesome 5 free';
        font-style: normal;
        font-weight: 600;
        position: absolute;
        right: 16px;
        top: 8px;
        font-size: 0.8rem;
    }
}

// cart super number class
.cart-no{
    position: relative;
    &:after{
        content: '3';
        color: var(--indigo);
        font-size: 0.9rem;
        font-weight: 700;
        position: absolute;
        left: 16px;
        top: -8px;
        font-family: Poppins,sans-serif;
    }
}

// cart icon in overlay layer
.cart-icon{
    position: absolute;
    &:after{
        content: '\f290';
        position: absolute;
        display: inline-block;
        background: #fff;
        font-family: 'Font Awesome 5 free';
        font-style: normal;
        font-weight: 600;
        color: #000;
        padding: 1.5rem 2rem;
        cursor: pointer;
        left: -78px;
    }
}
// no of items in cart box
.no-of-items{
    position: absolute;
    color: var(--indigo);
    font-weight: 700;
    font-size: 0.9rem;
    top: 13px;
    right: 23px;
    z-index: 20;
}